@media (orientation: portrait) {

    .chart {
        background: url('../../Components/ItemCard/images/tooltip.png');
        background-size: cover;
        background-repeat: no-repeat;
        border-radius: 15px;
        border-style: outset;
        border-color: lightgray;
        margin: 3vh;
        max-width: 90%;
        height: 30% !important;
    }

}

@media (orientation: landscape) {

    .chart {
        background: url('../../Components/ItemCard/images/tooltip.png');
        background-size: cover;
        background-repeat: no-repeat;
        border-radius: 10px;
        border-style: solid;
        border-color: grey;
        max-height: 28vw;
        max-width: 65vw;
        min-width: 68.5vw;
        margin: 3vh;
    }

}