@media (orientation: landscape) {

    @font-face {
        font-family: wow;
        src: url(../Fonts/LifeCraft_Font.ttf);
    }

    .main-view {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .alliance-logo {
        max-height: 20vh;
    }

    .site-title {
        color: white;
    }

    .main-menu {
        width: 30%;
        height: 10%;
    }

    .main-menu-card-body {
        display: flex;
        justify-content: center;
        height: 50%;
        background: url(../Images/inputBackground.jpg);
        background-size: cover;
        border-style: solid;
        border-radius: 15px;
        border-color: gray;
        padding: 3vh;
    }

    #search-input {
        width: 80%;
        font-size: 3vh;
    }

    .search-button {
        width: 20%;
        margin-left: 1vh;
        background: url(../Images/searchBackground.webp);
        color: white;
        font-family: wow;
        font-size: 2vh;
    }

}

@media (orientation: portrait) {

    @font-face {
        font-family: wow;
        src: url(../Fonts/LifeCraft_Font.ttf);
    }

    .main-view {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .alliance-logo {
        max-height: 25vh;
    }

    .site-title {
        color: white;
    }

    .main-menu-card-body {
        display: flex;
        justify-content: center;
        background: url(../Images/inputBackground.jpg);
        background-size: cover;
        border-style: solid;
        border-radius: 15px;
        border-color: gray;
        padding: 3vh;
    }

    .main-menu {
        width: 80%;
    }

    #search-input {
        padding: 2.6vw;
    }

    .search-button {
        color: white;
        font-family: wow;
        padding: 3vw;
        margin-left: 1vh;
        background: url(../Images/searchBackground.webp);
    }

}