@media (orientation: portrait) {

    @font-face {
        font-family: wow;
        src: url(../../Fonts/LifeCraft_Font.ttf);
    }

    .item-card {
        display: flex;
        background: url(../ItemCard/images/tooltip.png);
        background-size: cover;
        margin: 2vh;
        padding: 1vh;
        border-radius: 15px;
        border-style: outset;
        border-color: lightgray;
        max-width: 80%;
        min-width: 80%;
        font-weight: 600;
    }

    .result-cards {
        display: flex;
        flex-direction: column;
        align-items: center;
        overflow: scroll;
        margin-top: 1vh;
    }

    .type-div {
        display: flex;
        justify-content: space-between;
    }

    .card-title {
        padding: 8px 0px 0px 8px;
    }

    .binding, .requirements, .quantity, .type, .weapon-type, .sellPrice {
        color: white;
        padding: 0px 0px 0px 8px;
        margin: 0px 0px 0px 0px;
        margin-top: 0;
        font-size: 12px;
    }

    .itemPrice {
        color: white;
        padding: 0px 0px 0px 8px;
        margin-top: 0;
        font-size: 12px;
    }

    .item-level {
        color: #ffd100;
        padding: 0px 0px 0px 8px;
        margin: 0px 0px 5px 0px;
        font-size: 12px;
    }

    .useEffect {
        color: #1eff00;
        padding: 0px 0px 0px 8px;
        margin: 0px 8px 5px 0px;
        margin-top: 0;
        font-size: 12px;
    }

    .itemIcon {
        margin-top: 1.5vh;
        margin-left: 1.5vh;
        max-width: 6vh;
        min-width: 6vh;
        min-height: 6vh;
        max-height: 6vh;
        border-radius: 8px;
        border-width: 2px;
        border-style: outset;
        border-color: lightgray;
    }

    .item-details {
        margin-left: 1vh;
    }

}

@media (orientation: landscape) {

    @font-face {
        font-family: wow;
        src: url(../../Fonts/LifeCraft_Font.ttf);
    }

    .item-card {
        display: flex;
        margin: 2vh;
        padding: 1vh;
        max-width: 33%;
        min-width: 20%;
        max-height: 60%;
        cursor: pointer;
        font-family: Verdana,"Open Sans",Arial,"Helvetica Neue",Helvetica,sans-serif;
    }

    .result-cards {
        width: 95%;
        height: 55%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        overflow-y: scroll;
        overflow-x: hidden;
        margin-top: 3vh;
    }

    .result-cards::-webkit-scrollbar {
        display: none; /* for Chrome, Safari, and Opera */
    }

    .type-div {
        display: flex;
        justify-content: space-between;
        max-width: 95%;
    }

    .card-title {
        padding: 8px 0px 0px 8px;
    }

    .binding, .requirements, .quantity, .type, .weapon-type, .sellPrice {
        color: white;
        padding: 0px 0px 0px 8px;
        margin: 0px 0px 0px 0px;
        margin-top: 0;
        font-size: 12px;
    }

    .itemPrice {
        color: white;
        padding: 0px 0px 0px 8px;
        margin-top: 0;
        font-size: 12px;
    }

    .item-level {
        color: #ffd100;
        padding: 0px 0px 0px 8px;
        margin: 0px 0px 0px 0px;
        font-size: 12px;
    }

    .useEffect {
        color: #1eff00;
        padding: 0px 0px 0px 8px;
        margin: 0px 8px 5px 0px;
        margin-top: 0;
        font-size: 12px;
    }

    .itemIcon {
        margin-top: 0;
        max-width: 6vh;
        min-width: 6vh;
        min-height: 6vh;
        max-height: 6vh;
        border-radius: 8px;
        border-width: 2px;
        border-style: outset;
        border-color: lightgray;
    }

    .item-details {
        background: url(../ItemCard/images/tooltip.png);
        background-size: cover;
        width: 28vh;
        margin-left: 1vh;
        padding: 0.5vh;
        border-radius: 15px;
        border-style: outset;
        border-color: lightgray;
        border-width: 2px;
    }

}